.enroll {
  &__inner {
    display: flex;
    justify-content: flex-start;
    flex: 1;
    @media (max-width: 720px) {
      flex-direction: column;
    }
  }
  &__left {
    //width: 50%;
    text-align: center;
  }
  &__title {
    font-size: 33px;
    line-height: 43px;
    color: #333333;
    font-weight: 600;
    @media (max-width: 980px) {
      font-size: 30px;
      line-height: 40px;
    }
    @media (max-width: 720px) {
      font-size: 24px;
      line-height: 34px;
    }
    //@media (max-width: 440px) {
    //  font-size: 17px;
    //  line-height: 27px;
    //}
  }
  &__description {
    font-weight: 300;
    padding: 0 10px;
    font-size: 16px;
    line-height: 36px;
    @media (max-width: 980px) {
      line-height: 34px;
    }
    @media (max-width: 720px) {
      font-size: 14px;
      line-height: 30px;
    }
    @media (max-width: 440px) {
      line-height: 25px;
    }
  }
  &__right {
    min-width: 50%;
  }
}